import type { Delimiter } from '@ali/4ever-utils';

export function getMarkdownFromDelimiter(delimiter: Delimiter | Delimiter[], locale?: Record<string, string>) {
  if (!delimiter) return undefined;
  const delimiters = !Array.isArray(delimiter) ? [delimiter] : delimiter;
  const { character, size } = delimiters[0];
  return character.repeat(size) + (locale?.text || 'Text') + character.repeat(size) + ' ' + (locale?.space || 'Space');
}